
.combo_1{
    background-image: url("./comb1.png");
}

.combo_2{
    background-image: url("./comb2.png");
}

.combo_3{
    background-image: url("./comb3.png");
}

.history {
    width:125px;
    height:100px;

    border: none;
    position:relative;

    background-repeat:no-repeat;
    background-size:cover;
    background-position:center;

    background-color: rgb(220, 220, 220);

    border-style: solid;
    border-color: black;
    border-width: 1px;
    

    z-index: 1;
}

.history_top_text {
    width:125px;
    height:45px;
    
    background-color: white;
    font-size: 17px;
}

.history_description_text {
    width:125px;
    height:25px;
    font-size: 15px;

    background-color: white;
}

.history:hover {
    background-color: var(--secondary_color);;
}

.history_div {
    position: relative;
    top:-20px;
    width:100px;
    height:175px;
}

.history_bottom_parent_div {
    background-color: rgb(255, 255, 255, 0);
    width:125px;
    height:25px;

    position:relative;
    z-index: 0;
}


.history_bottom_div {
    animation: amount_slide_in 0.5s forwards;
    animation-delay: 0s;

    position: absolute;
    top:-25px;
    
    margin-top: -7px;

    background-color: white;
    width:125px;
    height:25px;

    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
}

@keyframes amount_slide_in {
    100% { top : 0px;}
}

.history_amount {
    position:absolute;
    top:0px;
    left:25px;

    padding: 0;
    margin:0;
    width:75px;
    height:25px;
    font-size: 18px;
    text-align: center;

    float:left;
}

.history_add_button {
    position:absolute;
    top:0px;
    left:100px;

    
    width:25px;
    height:25px;
    padding:0px;
    margin:0px;

    float:left;
    font-size:15px;
    text-align:center;

    border-style: solid;
    border-color: black;
    border-width: 2px;
    border-radius: 0px;
    border-bottom-right-radius: 5px;
}

.history_add_button:hover {
    background-color: white;
}

.history_subtract_button {
    position:absolute;
    top:0px;
    left:0px;

    width:25px;
    height:25px;
    padding:0px;
    margin:0px;

    float:left;

    font-size:15px;

    text-align:center;

    border-style: solid;
    border-color: black;
    border-width: 2px;
    border-radius: 0px;
    border-bottom-left-radius: 5px;
}

.history_subtract_button:hover {
    background-color: white;
}