

.transitbar_modal {
    outline-width: 5px;

    float:top;
    left:0;
    bottom:0px;
    position:fixed;
    z-index: 50;
    
    width:100%;
    height:160px;

    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.end_of_sim_div {
    position: fixed;

    left:25%;
    top:25%;

    width: 50%;
    height: 75px;

    margin: auto;

    border-style: solid;
    border-color: black;
    border-radius: 50px;

    background-color: white;
}

.end_of_sim {
    font-size: 50px;
}

