


.coffee {
    background-image: url("./coffee.png");
}

.frappe {
    background-image: url("./frappe.png");
}

.frappuccino {
    background-image: url("./frappuccino.png");
}

.iced_coffee {
    background-image: url("./iced_coffee.png");
}

.nitro {
    background-image: url("./nitro.png");
}

.tea {
    background-image: url("./tea.png");
}


.drink {
    width:125px;
    height:100px;

    border: none;
    position:relative;

    background-repeat:no-repeat;
    background-size:cover;
    background-position:center;

    background-color: rgb(220, 220, 220);

    border-style: solid;
    border-color: black;
    border-width: 1px;
    
    z-index: 2;
}

.edit_drink {
    width:150px;
    height:150px;
    border: none;

    margin-top: -25px;
    background-color: white;

    background-repeat:no-repeat;
    background-size:contain;
    background-position:center;
    background-blend-mode: normal;


    position: relative;
    z-index: 1;
}


.drink_edit_div {
    width:100px;
    height:100px;
    border: none;

    background-repeat:no-repeat;
    background-size:contain;
    background-position:center;
}

.drink_div {
    width:125px;
    height:175px;
}

.drink_bottom_parent_div {
    position:relative;
    width:125px;
    height:25px;
    background-color: rgb(255, 255, 255, 0);
    z-index: 0;
}

.drink_bottom_div {
    animation: amount_slide_in 0.5s forwards;
    animation-delay: 0s;

    position: absolute;
    top:-25px;
    
    margin-top: -7px;

    background-color: white;
    width:100%;
    height:25px;

    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
}

@keyframes amount_slide_in {
    100% { top : 0px;}
}

.drink_top_text {
    width:125px;
    height:25px;
    
    background-color: white;
    font-size: 20px;
}

.drink_description_text {
    width:125px;
    height:25px;
    font-size: 15px;

    background-color: white;
}

.drink:hover {
    background-color: var(--secondary_color);
}

.blocking_div {
    position: absolute;

    left:0px;
    top:0px;

    width:100%;
    height:100%;

    background-color: rgba(0, 0, 0, 0.0);

    animation: block_transparency 1s forwards ease;
    animation-delay: 0s;

    z-index: 80;
}

@keyframes block_transparency {
    100% { background-color: rgba(0, 0, 0, 0.7); }
}

.editing_div {
    animation: edit_slide 1s forwards ease;
    animation-delay: 0s;

    position: absolute;
    top: -300px;
    left: 50%;
    
    margin-top: -250px;
    margin-left: -200px;

    width:400px;
    height: 300px;

    background-color: var(--secondary_color);

    border-radius: 10px;

    z-index: 5;
}

@keyframes edit_slide {
    100% { top: 50%; }
}

.edit_bar_div {
    width:100%;
    height:50px;

    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: rgb(201, 201, 201);
}

.edit_top_text {
    top:5px;
    width:350px;
    height:50px;
    line-height: -5px;
    float:left;
}

.edit_text_attributes {
    position: relative;
    top:2px;
    line-height: -5px;
}

.edit_quit {
    position:relative;
    top:0%;
    float:left;

    width:50px;
    height:50px;

    background-image: url("./exit_button.png");
    background-repeat:no-repeat;
    background-size:contain;
    background-position:center;

    border-style: solid;
    border-color: black;
    border-width: 2px;
    border-radius: 10px;

    border-bottom-right-radius: 0px;
    background-color: gray;
}

.edit_quit:hover {
    background-color: white;
}


.edit_hot_cold_div {
    float:right;

    width:100px;
    height:150px;
}

.edit_hot {
    animation: edit_hot_to_not_selected 0.5s none ease;
    animation-delay: 0s;

    float:right;
    font-size: large;
    width:100px;
    height:75px;

    background-color: gray; 

    border-color: black;
    border-style: solid;
    border-width: 2px;
    border-radius: 0px;
    border-top-left-radius: 10px;

    font-weight: normal;
    font-size: large;
}

@keyframes edit_hot_to_not_selected {
    0% { 
        background-color: rgb(255, 90, 90);
        font-size:22px; 
    }
    100% {
        background-color: gray;
        font-size:large;
    }
}

.edit_hot:hover {
    background-color: rgb(160, 150, 150);
}



.edit_hot_selected {
    animation: edit_hot_to_selected 0.5s none ease;
    animation-delay: 0s;

    float:right;
    font-size: large;
    width:100px;
    height:75px;

    background-color: rgb(255, 90, 90); 

    border-color: black;
    border-style: solid;
    border-width: 2px;
    border-radius: 0px;
    border-top-left-radius: 10px;

    font-weight: bold;
    font-size: 22px;
}


@keyframes edit_hot_to_selected {
    0% {
        background-color: rgb(160, 150, 150);
        font-size:large;
    }
    100% { 
        background-color: rgb(255, 90, 90);
        font-size:22px; 
    }
}


.edit_cold {
    animation: edit_cold_to_not_selected 0.5s none ease;
    animation-delay: 0s;

    float:right;
    width:100px;
    height:75px;

    background-color: gray;

    border-color: black;
    border-style: solid;
    border-width: 2px;
    border-radius: 0px;
    border-top:0px;
    border-bottom-left-radius: 10px;

    font-weight: normal;
    font-size: large;
}

.edit_cold:hover {
    background-color: rgb(150, 150, 160);
}

@keyframes edit_cold_to_not_selected {
    0% { 
        background-color: rgb(103, 239, 252); 
        font-size: 22px;
    }
    100% { 
        background-color: gray; 
        font-size: large;
    }
}

.edit_cold_selected {
    animation: edit_cold_to_selected 0.5s none ease;
    animation-delay: 0s;

    float:right;
    font-size: large;
    width:100px;
    height:75px;

    background-color: rgb(103, 239, 252);

    border-color: black;
    border-style: solid;
    border-width: 2px;
    border-radius: 0px;
    border-top:0px;
    border-bottom-left-radius: 10px;

    font-weight: bold;
    font-size: 22px;
}

@keyframes edit_cold_to_selected {
    0% { 
        background-color: rgb(150, 150, 160);
        font-size: large;
    }
    100% { 
        background-color: rgb(103, 239, 252); 
        font-size: 22px;
    }
}


