

.pastries {
    min-height: 800px;
    width:100%;
    height:80%;

    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */

}

.pastries_grid {
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap:125px;
    row-gap: 0px;

    width:700px;
    height:651px;
    margin:auto;

    background-color: none;

    text-align: center;
    font-size: 30px;
    justify-content: center;
    align-self: center;

    background-image: url("./MS3_shelf_2.png");
    background-repeat:no-repeat;
    background-size:cover;
    background-position:center;  
}

.pastry-grid-item {
    display: flex;
    align-items: center;
    justify-content: center;
}

.pastry_text_div {
    position: relative;
    margin: auto;

    width:160px;
    height:65px;
}

.pastry_text {
    background-color: white;
    border-radius: 20px;
}