

.usuals {
    min-height: 800px;
    width:100%;
    height:80%;

    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.order_history_grid {
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap:125px;
    row-gap: 0px;

    width:700px;
    height:651px;
    margin:auto;

    background-color: none;

    text-align: center;
    font-size: 30px;
    justify-content: center;
    align-self: center;

    background-image: url("./MS3_shelf_2.png");
    background-repeat:no-repeat;
    background-size:cover;
    background-position:center;  
}


.grid-item {
    display: flex;
    align-items: center;
    justify-content: center;

    background-color: #dce0dd;
    border: 1px solid #666;
}

.drink_text {
    position: absolute;

    top: 270px;
    left: 50%;
    transform: translate(-50%, 0%);
    height:10px;
    width:700px;

    margin: 0 auto;

    text-align: center;
    justify-content: center;

    border-radius: 20px;
    border-color: black;
    border-style: inset;
    background-color: rgba(255, 255, 255, 1);
}

.sub_drink_text {
    position: absolute;

    top:-25px;
    width:100%;

    text-align: center;
    font-size: 30px;

    line-height: px;
    padding-bottom: 5px;
}


.top_drink_text_div {
    position: relative;
    margin: auto;

    width:380px;
    height:65px;
}

.top_drink_text {
    background-color: white;
    border-radius: 20px;
}