

.order_element{
    position: relative;
    width:700px;
    height:100px;

    text-align: center;
    /*  Center content    */
    display: flex;
    justify-content: left;

    border-width: 2px;
    border-style: solid;

    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version*/

}

.gray_div {
    position:absolute;
    width:100%;
    height:100%;

    background-color: rgba(0, 0, 0, 0);
    z-index: 0;

    animation: gray_div_insert 1s forwards;
    animation-delay: 0s;
}

@keyframes gray_div_insert{
    100% { background-color: rgba(0, 0, 0, 0.4); }
}

.gray_div_gone {
    position:absolute;
    width:100%;
    height:100%;

    background-color: rgba(0, 0, 0, 0.4);
    z-index: 0;

    animation: gray_div_remove 1s forwards;
    animation-delay: 0s;
}

@keyframes gray_div_remove {
    100% { background-color: rgba(0, 0, 0, 0);; }
}


.item_detail {
    position:relative;
    width:400px;
    height:100px;
    z-index: -1;

    background-color: white;
}

.add_item {
    position: relative;
    z-index: 1;

    width:100px;
    height:100px;

    border-style: solid;
    border-color: black;
    border-radius: 0px;
    border-width: 0px;
    border-left-width: 2px;
    border-right-width: 2px;

    font-size: 50px;
    line-height: 0px;
    padding-bottom: 4px;
}

.add_item:hover {
