.Customization {
  position:relative;

  width:900px;
  height:800px;

  margin:auto;

  -webkit-user-select: none; /* Safari */        
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}
  
  .Add {
    grid-area: add;
    align-self: center;
  }

  .LeftSide {
    display: flex;
    flex-direction: column;
  }
  
  .MiniDist {
    padding-top: 5%;
  }
  
  .TempPadding {
    padding-left: 13%;
  }
  
  .LargeBtn {
    width: 100px;
    height: 50px;
  }
  
  .Pulse {
    animation: pulse-animation 2s 1
  }
  
  @keyframes pulse-animation {
    0% {
      box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.2);
    }
    100% {
      box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
    }
  }
  
  .MiniGap {
    gap: 10%;
  }
  
  .Inactive {
    background-color: #41bdad;
    font-size: 16px;
  }
  
  .Active {
    background-color: red;

    font-size: 18px;
    font-weight: bold;
  }
  
  .SizePadding {
    padding-left: 15%;
  }
  
  .FlexRow {
    display: flex;
    align-self: flex-start;
  }
  
  .Gap {
    gap: 30%;
  }
  
  .SmallScale {
    height: 100px; 
    width: 100px;
  }
  
  .Moddifiers {
    grid-area: moddifiers;
  }
  
  .LargerTxt {
    font-size: 30px;
  }
  
  .CubesOffset {
    padding-left: 4.2%;
  }
  
  .Selection {
    grid-area: selection;
    display: flex;
    justify-self: center;
    align-content: center;
    display: inline-flex;
  }
  
  .Rectangle {
      border: 1px outset blue;
      background-color: lightBlue;
      height:75%;
      width:75px;
      cursor:pointer;
  }
  
  .Cup {
    grid-area: cup;
    display: flex;
    justify-content: center;
    scale: 60%;
    transform: translateY(-135px);
    padding-top: 2.5%;
  }
  
  .Photo > img {
    object-fit: fill;
    z-index: 5;
  }
  
  .SmallPhoto {
    transform: translateY(-50px);
    scale: 30%;
    padding-top: 11%;
  }
  
  .MediumPhoto {
    scale: 45%;
    padding-top: 6%;
  }
  
  .Rotate {
    transform: rotate(-90deg);
  }
  
  .Box {
    border: 1px outset blue;
    background-color: lightBlue;
    height:50%;
    width:50%;
    cursor:pointer;
  }
  
  .SizeSelector {
    justify-content: center;
    justify-self: end;
  }
  
  .StackVerti {
    display: flex;
    flex-direction: column;
  }
  
  .container {
    width: 400px;
    height: 800px;
    position: absolute;
    top: 50px;
    left: 25px;
  }

  .coffee-header {
    width: 100%;
    height: 30%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #ddcfcc;
    border-radius: 10px;
  }
  .coffee-header__buttons {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 25px;
    background-color: #282323;
    border-radius: 50%;
  }
  .coffee-header__buttons::after {
    content: "";
    width: 8px;
    height: 8px;
    position: absolute;
    bottom: -8px;
    left: calc(50% - 4px);
    background-color: #615e5e;
  }
  .coffee-header__button-one {
    left: 15px;
  }
  .coffee-header__button-two {
    left: 50px;
  }
  .coffee-header__display {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 20%;
    left: calc(50% - 25px);
    border-radius: 50%;
    background-color: #9acfc5;
    border: 5px solid #43beae;
    box-sizing: border-box;
    text-align: center;
    
  }
  .coffee-header__details {
    width: 8px;
    height: 20px;
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #9b9091;
    box-shadow: -12px 0 0 #9b9091, -24px 0 0 #9b9091;
  }
  .coffee-medium {
    width: 90%;
    height: 50%;
    position: absolute;
    top: 30%;
    left: calc(50% - 45%);
    background-color: #bcb0af;
  }
  
  .coffe-medium__exit {
    width: 60px;
    height: 20px;
    position: absolute;
    top: 0;
    left: calc(50% - 30px);
    background-color: #231f20;
  }
  .coffe-medium__exit::before {
    content: "";
    width: 50px;
    height: 20px;
    border-radius: 0 0 50% 50%;
    position: absolute;
    bottom: -20px;
    left: calc(50% - 25px);
    background-color: #231f20;
  }
  .coffe-medium__exit::after {
    content: "";
    width: 10px;
    height: 10px;
    position: absolute;
    bottom: -30px;
    left: calc(50% - 5px);
    background-color: #231f20;
  }
  .coffee-medium__cup {
    width: 80px;
    height: 47px;
    position: absolute;
    bottom: 0;
    left: calc(50% - 40px);
    background-color: #FFF;
    border-radius: 0 0 70px 70px / 0 0 110px 110px;
  }
  .coffee-medium__cup::after {
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    top: 6px;
    right: -13px;
    border: 5px solid #FFF;
    border-radius: 50%;
  }
  @keyframes liquid {
    0% {
      height: 0px;  
      opacity: 1;
    }
    5% {
      height: 0px;  
      opacity: 1;
    }
    20% {
      height: 80%;  
      opacity: 1;
    }
    95% {
      height: 83%;
      opacity: 1;
    }
    100% {
      height: 83%;
      opacity: 0;
    }
  }
  .coffee-medium__liquid {
    width: 6px;
    height: 63px;
    opacity: 0;
    position: absolute;
    top: 50px;
    left: calc(50% - 3px);
    background-color: #74372b;
  }
  
  .Pour {
    animation: liquid 4s linear 1;
  }
  
  .coffee-footer {
    width: 95%;
    height: 15px;
    position: absolute;
    bottom: 21%;
    left: calc(50% - 47.5%);
    background-color: #41bdad;
    border-radius: 10px;
    z-index: 6;
  }
  .coffee-footer::after {
    content: "";
    width: 103%;
    height: 23px;
    position: absolute;
    bottom: -20px;
    left: -8px;
    background-color: #000;
  }
  
  .CoffeeBtns {
    display: flex;
    justify-content: center;
    padding-top: 20%;
  }

  .CenteredBtn {
    position: fixed;
    top: 50%;
    left: 50%;
  }